import React, { useState, useEffect } from 'react'
import { getUsers } from '@/service/Weight'

export default function WeightView() {
  const [users, setUsers] = useState([])
  
  const init = async () => {
    const users = await getUsers()
    console.log(users)
    setUsers(users)
  }

  useEffect(() => {
    init()
  }, [])

  return (
    <div className='weight-content'>
      1
    </div>
  )
}